// Types
export type Board = number[][];
export type Direction = 'left' | 'right' | 'up' | 'down';

// Initialize an empty board
export const initializeBoard = (): Board => {
  const board: Board = Array(4).fill(null).map(() => Array(4).fill(0));
  return addNewTile(addNewTile(board)); // Add two initial tiles
};

// Get empty cells from the board
const getEmptyCells = (board: Board): [number, number][] => {
  const emptyCells: [number, number][] = [];
  for (let i = 0; i < 4; i++) {
    for (let j = 0; j < 4; j++) {
      if (board[i][j] === 0) {
        emptyCells.push([i, j]);
      }
    }
  }
  return emptyCells;
};

// Add a new tile (2 or 4) to a random empty cell
export const addNewTile = (board: Board): Board => {
  const newBoard = board.map(row => [...row]);
  const emptyCells = getEmptyCells(newBoard);
  
  if (emptyCells.length === 0) return newBoard;
  
  const [row, col] = emptyCells[Math.floor(Math.random() * emptyCells.length)];
  newBoard[row][col] = Math.random() < 0.9 ? 2 : 4; // 90% chance of 2, 10% chance of 4
  
  return newBoard;
};

// Compress the row (move all non-zero numbers to the left)
const compress = (row: number[]): number[] => {
  const newRow = row.filter(cell => cell !== 0);
  while (newRow.length < 4) newRow.push(0);
  return newRow;
};

// Merge identical adjacent numbers in a row
const merge = (row: number[]): { row: number[]; score: number } => {
  let score = 0;
  for (let i = 0; i < 3; i++) {
    if (row[i] !== 0 && row[i] === row[i + 1]) {
      row[i] *= 2;
      score += row[i];
      row[i + 1] = 0;
    }
  }
  return { row: compress(row), score };
};

// Move left
export const moveLeft = (board: Board): { board: Board; score: number } => {
  const newBoard: Board = [];
  let score = 0;
  
  for (let i = 0; i < 4; i++) {
    const { row, score: rowScore } = merge(compress(board[i]));
    newBoard.push(row);
    score += rowScore;
  }
  
  return { board: newBoard, score };
};

// Rotate the board 90 degrees clockwise
const rotateBoard = (board: Board): Board => {
  const newBoard: Board = Array(4).fill(null).map(() => Array(4).fill(0));
  for (let i = 0; i < 4; i++) {
    for (let j = 0; j < 4; j++) {
      newBoard[j][3 - i] = board[i][j];
    }
  }
  return newBoard;
};

// Move right
export const moveRight = (board: Board): { board: Board; score: number } => {
  const rotated = rotateBoard(rotateBoard(board));
  const { board: moved, score } = moveLeft(rotated);
  return { board: rotateBoard(rotateBoard(moved)), score };
};

// Move up
export const moveUp = (board: Board): { board: Board; score: number } => {
  const rotated = rotateBoard(rotateBoard(rotateBoard(board)));
  const { board: moved, score } = moveLeft(rotated);
  return { board: rotateBoard(moved), score };
};

// Move down
export const moveDown = (board: Board): { board: Board; score: number } => {
  const rotated = rotateBoard(board);
  const { board: moved, score } = moveLeft(rotated);
  return { board: rotateBoard(rotateBoard(rotateBoard(moved))), score };
};

// Check if game is over
export const isGameOver = (board: Board): boolean => {
  // Check for empty cells
  if (getEmptyCells(board).length > 0) return false;
  
  // Check for possible merges horizontally
  for (let i = 0; i < 4; i++) {
    for (let j = 0; j < 3; j++) {
      if (board[i][j] === board[i][j + 1]) return false;
    }
  }
  
  // Check for possible merges vertically
  for (let i = 0; i < 3; i++) {
    for (let j = 0; j < 4; j++) {
      if (board[i][j] === board[i + 1][j]) return false;
    }
  }
  
  return true;
};
