"use client";

import React, { useState, useEffect, useCallback } from 'react';
import { Button } from "@/components/ui/button";
import { Dialog, DialogContent, DialogHeader, DialogTitle, DialogDescription } from "@/components/ui/dialog";
import {
  initializeBoard,
  moveLeft,
  moveRight,
  moveUp,
  moveDown,
  addNewTile,
  isGameOver,
  type Board,
  type Direction
} from '@/lib/game2048Logic';

const Game2048 = () => {
  const [board, setBoard] = useState<Board>(Array(4).fill(null).map(() => Array(4).fill(0)));

  // Initialize board after mount to avoid hydration mismatch
  useEffect(() => {
    setBoard(initializeBoard());
  }, []);
  const [score, setScore] = useState<number>(0);
  const [gameOver, setGameOver] = useState<boolean>(false);
  const [highScore, setHighScore] = useState<number>(0);

  // Load high score from localStorage on mount
  useEffect(() => {
    const savedHighScore = localStorage.getItem('2048-high-score');
    if (savedHighScore) {
      setHighScore(parseInt(savedHighScore));
    }
  }, []);

  // Update high score when current score exceeds it
  useEffect(() => {
    if (score > highScore) {
      setHighScore(score);
      localStorage.setItem('2048-high-score', score.toString());
    }
  }, [score, highScore]);

  const handleMove = useCallback((direction: Direction) => {
    if (gameOver) return;

    let moveResult;
    switch (direction) {
      case 'left':
        moveResult = moveLeft(board);
        break;
      case 'right':
        moveResult = moveRight(board);
        break;
      case 'up':
        moveResult = moveUp(board);
        break;
      case 'down':
        moveResult = moveDown(board);
        break;
      default:
        return;
    }

    const newBoard = moveResult.board;
    const hasChanged = JSON.stringify(board) !== JSON.stringify(newBoard);

    if (hasChanged) {
      const boardWithNewTile = addNewTile(newBoard);
      setBoard(boardWithNewTile);
      setScore(score + moveResult.score);
      
      if (isGameOver(boardWithNewTile)) {
        setGameOver(true);
      }
    }
  }, [board, gameOver, score]);

  const handleKeyDown = useCallback((event: KeyboardEvent) => {
    switch (event.key) {
      case 'ArrowLeft':
        handleMove('left');
        break;
      case 'ArrowRight':
        handleMove('right');
        break;
      case 'ArrowUp':
        handleMove('up');
        break;
      case 'ArrowDown':
        handleMove('down');
        break;
    }
  }, [handleMove]);

  useEffect(() => {
    window.addEventListener('keydown', handleKeyDown);
    return () => {
      window.removeEventListener('keydown', handleKeyDown);
    };
  }, [handleKeyDown]);

  const resetGame = () => {
    setBoard(initializeBoard());
    setScore(0);
    setGameOver(false);
  };

  const getTileColor = (value: number): string => {
    const colors: { [key: number]: string } = {
      2: 'bg-gray-200 text-gray-800',
      4: 'bg-gray-300 text-gray-800',
      8: 'bg-orange-200 text-white',
      16: 'bg-orange-300 text-white',
      32: 'bg-orange-400 text-white',
      64: 'bg-orange-500 text-white',
      128: 'bg-yellow-300 text-white',
      256: 'bg-yellow-400 text-white',
      512: 'bg-yellow-500 text-white',
      1024: 'bg-yellow-600 text-white',
      2048: 'bg-yellow-700 text-white'
    };
    return colors[value] || 'bg-yellow-800 text-white';
  };

  return (
    <div className="flex flex-col items-center justify-center min-h-screen bg-gray-100 p-4">
      <div className="w-full max-w-md space-y-6">
        <div className="flex justify-between items-center">
          <div className="space-y-1">
            <h1 className="text-4xl font-bold text-gray-900">2048</h1>
            <p className="text-sm text-gray-600">Join the numbers and get to the 2048 tile!</p>
          </div>
          <Button 
            onClick={resetGame}
            className="bg-gray-800 hover:bg-gray-700 text-white"
          >
            New Game
          </Button>
        </div>

        <div className="flex justify-between items-center bg-white rounded-lg p-4 shadow-sm">
          <div className="text-center">
            <div className="text-sm text-gray-600">Score</div>
            <div className="text-2xl font-bold text-gray-900">{score}</div>
          </div>
          <div className="text-center">
            <div className="text-sm text-gray-600">Best</div>
            <div className="text-2xl font-bold text-gray-900">{highScore}</div>
          </div>
        </div>

        <div className="bg-gray-800 p-4 rounded-lg">
          <div className="grid grid-cols-4 gap-3">
            {board.map((row, i) =>
              row.map((cell, j) => (
                <div
                  key={`${i}-${j}`}
                  className={`
                    w-full aspect-square rounded-lg flex items-center justify-center
                    text-2xl font-bold transition-all duration-100
                    ${cell === 0 ? 'bg-gray-700' : getTileColor(cell)}
                  `}
                >
                  {cell !== 0 && cell}
                </div>
              ))
            )}
          </div>
        </div>

        <div className="text-sm text-gray-600 text-center">
          Use arrow keys to move tiles. When two tiles with the same number touch, they merge into one!
        </div>
      </div>

      <Dialog open={gameOver} onOpenChange={(open) => !open && resetGame()}>
        <DialogContent>
          <DialogHeader>
            <DialogTitle>Game Over!</DialogTitle>
            <DialogDescription>
              Your score: {score}
              {score === highScore && score > 0 && " (New High Score!)"}
            </DialogDescription>
          </DialogHeader>
          <div className="flex justify-end">
            <Button onClick={resetGame}>
              Play Again
            </Button>
          </div>
        </DialogContent>
      </Dialog>
    </div>
  );
};

export default Game2048;
